
h1{
  z-index: auto;
  margin:0;
  text-align: center;
  background-color: rgb(159, 119, 196);
  font-family: 'Crimson Pro', serif;
  font-weight:bold;
  font-size: 2rem;
  color:white;
  position: fixed;
  width:100%;
  height:100px;
}
hr{
  color:black;
}
span {
  z-index:0;
  color:black;
  position: relative;
  font-family: 'Roboto Mono', monospace;
  width: 50%;
}
span:hover {
  color:rgb(146, 10, 10);
  position: relative;
  font-family: 'Roboto Mono', monospace;
  font-weight:bold;
  font-variant: small-caps;
  font-size: 20px;
  width: 50%;

}
img{
  z-index:0;
  width: 100%;
  color: #ffffff;
  max-width: 255px;
  height:350px;
}
/* example of z indexing */
/* img:hover{
  background-color:palevioletred;
} */
img {
  background-color:rgb(230, 230, 230);
  opacity: 0.8;
  z-index:2;
}


